.round-sling-card .equipment-title {
  background-color: #FF5722;  /* ÐžÑ€Ð°Ð½Ð¶ÐµÐ²Ñ‹Ð¹ Ñ†Ð²ÐµÑ‚ Ð´Ð»Ñ Round Sling */
}

.round-sling-card .equipment-image {
  background-color: #FFF3E0;  /* Ð¡Ð²ÐµÑ‚Ð»Ð¾-Ð¾Ñ€Ð°Ð½Ð¶ÐµÐ²Ñ‹Ð¹ Ñ„Ð¾Ð½ */
}

.round-sling-card .equipment-image svg {
  color: #FF5722;  /* ÐžÑ€Ð°Ð½Ð¶ÐµÐ²Ñ‹Ð¹ Ñ†Ð²ÐµÑ‚ Ð¸ÐºÐ¾Ð½ÐºÐ¸ */
