/* ÐÐ´Ð°Ð¿Ñ‚Ð¸Ð²Ð½Ñ‹Ð¹ Ð´Ð¸Ð·Ð°Ð¹Ð½ ÐºÐ°Ñ€Ñ‚Ð¾Ñ‡ÐºÐ¸ */
@media (min-width: 640px) {
  .equipment-content {
