  }
}

/* ÐÐ´Ð°Ð¿Ñ‚Ð¸Ð²Ð½Ð°Ñ ÑÐµÑ‚ÐºÐ° ÐºÐ°Ñ€Ñ‚Ð¾Ñ‡ÐµÐº */
.equipment-cards-grid {
  display: grid;
  grid-template-columns: repeat(3, minmax(330px, 1fr));
  gap: 20px;
  margin: 30px auto;
  max-width: 1200px;
}

/* ÐÐ´Ð°Ð¿Ñ‚Ð°Ñ†Ð¸Ñ Ðº ÐºÐ¾Ð»Ð¸Ñ‡ÐµÑÑ‚Ð²Ñƒ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð² */
.equipment-cards-grid.items-1 {
  grid-template-columns: minmax(330px, 700px);
  justify-content: center;
}

/* Ð”Ð»Ñ 2 ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð² - Ð¾Ð±Ð½Ð¾Ð²Ð»ÐµÐ½Ð¾ */
.equipment-cards-grid.items-2 {
  grid-template-columns: repeat(2, minmax(330px, 500px));
  justify-content: center;
}

/* 3 ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð° Ð·Ð°Ð½Ð¸Ð¼Ð°ÑŽÑ‚ ÑÑ‚Ñ€Ð¾ÐºÑƒ Ñ†ÐµÐ»Ð¸ÐºÐ¾Ð¼ - Ð¾Ð±Ð½Ð¾Ð²Ð»ÐµÐ½Ð¾ */
.equipment-cards-grid.items-3 {
  grid-template-columns: repeat(3, 1fr);
}

/* 4 ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð°: 2 Ð² Ð¿ÐµÑ€Ð²Ð¾Ð¼ Ñ€ÑÐ´Ñƒ, 2 Ð²Ð¾ Ð²Ñ‚Ð¾Ñ€Ð¾Ð¼ - Ð¾Ð±Ð½Ð¾Ð²Ð»ÐµÐ½Ð¾ */
.equipment-cards-grid.items-4 {
  grid-template-columns: repeat(2, 1fr);
  max-width: 1000px;
}

/* 5 ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð²: 3 Ð² Ð¿ÐµÑ€Ð²Ð¾Ð¼ Ñ€ÑÐ´Ñƒ, 2 Ð¿Ð¾ Ñ†ÐµÐ½Ñ‚Ñ€Ñƒ Ð²Ð¾ Ð²Ñ‚Ð¾Ñ€Ð¾Ð¼ - Ð¾Ð±Ð½Ð¾Ð²Ð»ÐµÐ½Ð¾ */
.equipment-cards-grid.items-5 {
  grid-template-columns: repeat(3, 1fr);
}

.equipment-cards-grid.items-5 > :nth-child(4),
.equipment-cards-grid.items-5 > :nth-child(5) {
  justify-self: center;
}

/* Ð¡Ñ‚Ð¸Ð»Ð¸ Ð´Ð»Ñ ÐºÐ°Ñ€Ñ‚Ð¾Ñ‡ÐµÐº Ñ‚Ð¾Ð²Ð°Ñ€Ð¾Ð² - Ð¾Ð±Ð½Ð¾Ð²Ð»ÐµÐ½Ð¾ */
.catalog-item {
  width: 100%;
  max-width: none;
}

/* ÐÐ´Ð°Ð¿Ñ‚Ð¸Ð²Ð½Ð¾ÑÑ‚ÑŒ Ð´Ð»Ñ Ð¿Ð»Ð°Ð½ÑˆÐµÑ‚Ð¾Ð² */
@media (max-width: 1024px) {
  .equipment-cards-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
  }
  
  .equipment-main-title {
    font-size: 30px;
    margin: 30px 0;
  }
}

/* ÐÐ´Ð°Ð¿Ñ‚Ð¸Ð²Ð½Ð¾ÑÑ‚ÑŒ Ð´Ð»Ñ Ð¼Ð¾Ð±Ð¸Ð»ÑŒÐ½Ñ‹Ñ… ÑƒÑÑ‚Ñ€Ð¾Ð¹ÑÑ‚Ð² */
@media (max-width: 767px) {
  .equipment-cards-grid {
    grid-template-columns: 1fr;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
    gap: 15px;
  }
  
  .equipment-main-title {
    font-size: 24px;
    margin: 25px 0;
    padding-bottom: 10px;
  }
  
  .equipment-main-title:after {
